@import 'https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap';

.containerPesca {

  /* background-image: url(/src/imgs/marBarco.png); */
  /* background-image: url(/src/imgs/barcopesca.svg); */
  background-image: url(/src/imgs/fundoBarco3.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  /* background-size: 100% 1100px; */

  min-height: 100vh;

  /* height: 65vh; */
  width: 100%;
  position: relative;


  padding-top: 25px;
  padding-bottom: 20px;
}

.divBarco {
  margin: auto;
  width: 100%;
  height: 425px;
  overflow: hidden;

  /* margin-left: -200px; */
}

.divBarco div {
  /* width: 120%; */
  height: 375px;
  overflow: hidden;
}

.containerPesca::after {
  content: " ";

  position: absolute;
  top: 0;

  width: 100%;
  height: 100%;

  z-index: 2;

  background-color: rgba(0, 0, 0, 0.6);
}

.containerPesca * {
  z-index: 3;
  position: relative;
}

.divInfos {
  width: 90%;

  margin: auto;

  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  padding: 30px;

  border-radius: 10px;
  border: 4px solid white;
  color: white;

  margin-bottom: 20px;
}

.divInfos div {
  flex: 1 1 20%;

  display: flex;
  flex-flow: column nowrap;
  align-items: center;



  border-right: 4px solid white;

  font-family: 'Mulish', sans-serif;
  font-weight: 600;
}

.divInfos div:last-child {
  border: none;
}

.divInfos div p {
  width: 80%;
  font-size: 1.2rem;
  text-align: center;
}

.divInfos div h2 {
  font-size: 4rem;
}

