@media screen and (max-width: 1024px) {
  #borda {
    margin-top: -700px;
    width: 800px;
    margin-left: 100px;
  }

  #textoBarco {
    width: 800px;
    margin-top: -700px;
    margin-left: -800px;

  }

  /* #tresd{
      margin-top: -350px;
    } */

  .textoPesca {
    /* position: absolute;
      margin-top: -980px; */
    width: 100%;
    /* height: 100%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3em;
    color: #fff;
    text-align: center;
  }

  .porcentagem {
    display: flex;

  }

  .containerPesca{
    min-height: auto;
    height: auto;
    background-color: white;
    background-image: none;
  }

  .containerPesca p, .containerPesca h2{
    color: black;
  }

  .containerPesca::after{
    display: none;
  }

  .divBarco{
    display: none;
  }

  .divInfos{
    flex-flow: column nowrap;
    border: none;
  }

  .divInfos div{
    border: none;
    min-width: 50%;
  }

  .divInfos div:not(div:last-child)::after{
    content: "";
    color: rgba(0, 0, 0, 0.486);
    font-weight: 200;
    background-color: black;
    width: 3px;
    height: 50px;
    border-radius: 20px;
    margin: 10px 0;
  }

}

.barcoPesca {
  width: 100%;
  height: auto;
}

